body {
    margin: 0;
    background-color: black;
    font-family: Arial, Helvetica, sans-serif;
}

.button {
    background-color: white;
    color: black;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

#start-button {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 200px;
    height: 75px;
    font-size: 2em;
}

#fullscreen-button {
    position: absolute;
    top: 10px;
    left: 10px;
    
    width: 100px;
    height: 40px;
}